@media only screen and (max-width: 484px) {
    .simple-menu{
        display: block;
        position: fixed;
        left: 0;
        top: 30px;
        z-index: 999999;
        background-color: rgba(165, 165, 165, 0.3);
    }
    .imgTranslate{
        width: 30px;
        height: 30px;
    }
    .nav{
        display: none;
    }
    .links{
        font-size: 0.8rem;
    }
    .nav__language{
        display: none!important;
    }
    .phoneChangeLang{
        display: flex;
  justify-content: space-between;
        align-items: center;

    }
    .phoneChangeLang p{
        margin-top: 10px;
    }
}
