
  .mainContainer{
      display: flex;
      justify-content: center;
      width: 100%;
    max-height: 100%;
  padding: 10px;
  flex-direction: column;
transition: 1s ease;
    background-color: #d2d2d2;
      background-size: 100%, 100%, cover;
      background-repeat: no-repeat;
  color: white;
  text-align: center;
  }
  .wrap__project{
  margin-top: 100px;
    color: black;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;


  }
  .info-project{
    margin-bottom: 30px;
  }
  .info-project p{
    font-size: 20px;
  }
  .project__item{
    width: 100%;
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
    flex-direction: column;
align-items: center;

  }
.item{
margin-bottom: 25px;
width: 28%;
}
.btnModal{
  width: 100%;
  background-color: rgb(255, 255, 255,0.70) !important;
  margin-top: 5px !important;
}
.public{
  display: flex;
  padding: 5px;
  background-color: white;
  align-items: center;
  margin-top: 10px;
  justify-content: space-between;
}
  .public p{
    margin-top: 10px;
    color: #9f9f9f;

  }



  .MuiPaper-root{
  background-color:  rgb(255, 255, 255,0.70) !important;
}
  .MuiTypography-colorTextSecondary{
    text-align: justify;
  }
  .MuiLinearProgress-root{
    height: 8px !important;
  }
.preloader{
  width: 100%;
  height: 100vh;
  background-color: white;
  display: flex;
  justify-content: center;
  align-items: center;
  transition: 1s ease;
}


.form{
  transition: 1s ease;
  background-color:  rgb(255, 255, 255,0.30);
  padding: 0.2rem;
  font-size: 1rem;
  width: 40%;
margin: 0 auto;
display: flex;
justify-content: center;
align-items: center;

}
.form:hover{
  background-color:  rgb(255, 255, 255,0.50);
}
.form a{
  text-decoration: none !important;
  color: black;
  margin: 0 auto;
}
.form p{
  display: flex;
  justify-content: center;
align-items: center;
  margin: 0;
  padding: 5px;
}
.form a:hover{
  text-decoration: underline !important;
  color: gold;
}
.textSort{
  font-size: 15px !important;
}
.likeIcon{
  transition: 1s ease;
  width: 25px;
  height: 25px;
  filter: none;
}
  .unLike{
    transition: 0.2s ease;
    width: 25px;
    height: 25px;
    filter: grayscale(.75)
  }
  .likeIcon:hover{
    transform: scale(1.2);
  }

.likeBlock{
  width: 100px;
  display: flex;
  align-items: center;
justify-content: space-evenly;
}
