
.nav{
    position: fixed;
    width: 100%;
z-index: 100;
    display: flex;
    align-items: center;
    justify-content: space-around;
    padding: 1vh;
    text-align: center;
    margin: 0 auto;
    border-bottom: 1px solid black;
    background-color:  rgb(255, 255, 255,0.70);
}
.links{
    text-decoration: none;
    font-size: 1.5rem;

    color: black;
    transition: 0.5s ease;
    border-bottom: none;
}
.links:hover{
    color: gold;
}
.nav__language{
    color: black !important;
}
.phoneChangeLang{

    display: none;
    width: 100%;
    height: 20px;
    text-align: left;
}
.phoneChangeLang p,img{
  width: 40px;
}
.selected{
    transition: 0.5s ease;
    color: gold;
    border-bottom: 1px solid black;
}
.simple-menu{
    display: none;

}
.MuiPopover-paper{
    top: 75px !important;

}
