
.mainPhoto{
    width: 100%;
}
.main{
width: 100vw;
background-image: url(../../images/new.png);
 background-size: 100%,100%;
display: flex;
align-items: center;
justify-content: center;
color: #000000;
    font-family: "PT Sans";
    background-repeat: no-repeat;
}
.main__details{
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    height: auto;
}
.main__item{
margin-bottom: 10vh;
}
.wrapInfo{
    display: flex;
    align-items: center;
    flex-direction: column;
    margin-top: 150px;
}
.detail{
    letter-spacing: 2vh;
    font-size: 1rem;
    line-height: 50px;
    text-align: center;
    font-weight: bold;
}
.aboutAnn{
font-size: 50px;
    text-align: center;
}

.main__instagram{
   color: black;
   text-decoration: none;
   
   font-size: 20px;
}
.main__instagram:hover{
    color: gold;
}
.instagram_wrap{
    width: 250px;
    height: 250px;
    border: 3px solid rgba(146, 146, 146, 0.74);
    border-radius: 50%;
    display: flex;
    justify-content: center;
    align-items: center;
    color: white;
}
.instagram__iconlink{
    font-size: 18px;
    color: #1C2430;
    font-weight: 700;
}
.main__more{
display: flex;
    justify-content: center;
    height: 100%;
}
.more__item{
    width: 100%;
    display: flex;
    justify-content: center;
    flex-wrap: wrap;
    align-content: flex-start;
    height: 100%;
}
.more__item div{
    width: 50%;
}
.item-text{
    background-color: rgba(255, 255, 239);
    line-height: 30px;
    display: flex;
    justify-content: center;
    align-items: center;
}
.item-text p{
    width: 320px;
text-align: justify;
}

.bcg__more img{
    width: 100%;
    height: 700px;
}
.more__item div:nth-child(3n){
    order: 0;
}
