
.wrapLink{
    display: flex;
    justify-content: space-around;
    align-items: center;
width: 100%;
    height: 719px;
    background-image: url("../../images/artsMain.jpg");
    background-size: 100% 100%;
transition: 0.5s ease;
}
.wrapLinkTwo{
    display: flex;
    justify-content: space-around;
    align-items: center;
    width: 100%;
    height: 719px;
    background-image: url("../../images/clothesMain.jpg");
    background-size: 100% 100%;
    transition: 0.5s ease;
}
.active{
    color: gold!important;
    border-bottom: 1px solid black!important;
    transition: 0.5s ease;
}

.wrap__item{
    display: flex;
    justify-content: space-between;
    align-items: center;
    width: 50%;
    padding: 30px;
    margin-top: 0;
background-color: rgb(255, 255, 255,0.30);
    border-radius: 10px;
    box-shadow: 0 0 50px rgba(0,0,0,0.9);
}


.link__item{
    font-size: 30px;
    color: black;
    text-decoration: none;
    transition: 0.5s ease;
    border-bottom: none;
}
.link__item:hover{
    color: gold;
}
.works{
    display: flex;
    justify-content: center;
    flex-direction: column;

    width: 100vw;

align-items: center;

}
.works__picture{
    width: 100%;
    display: flex;
    justify-content: center;
align-items: center;
    margin-bottom: 70px;
}
.imageSize{
    width: 290px;
    height: 400px;

}
.imageSize img{
    width: 290px;
    height: 400px;
}

.wrapWorkComponents h2{
    text-align: center;
}
.itemWrap{
    display: flex;
    justify-content: center;
    align-items: center;
    flex-wrap: wrap;
}
.itemWork{
    margin-right: 20px;
    margin-bottom: 20px;
}
.sliderWrap{
    width: 40vw;
}

.slick-prev:before, .slick-next:before{
    background-color: black !important;
    border-radius: 50%;

}
.sliderWrap img{
    width: 100%;
    height: 80vh ;
}

