
.contactWrap{
    display: flex;
    justify-content: center;
    align-items: center;
    height: 100vh;
    flex-direction: column;
    width: 100%;
    background-color: #d2d2d2;
}
.contact-form{
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    width: 70%;
}
.sendMessage{
    width: 50%;
    margin-top: 20px!important;
}
.form__item{
    width: 100%;
    margin-bottom: 70px!important;
}
.lastForm{
    margin-bottom: 0 !important;

}
.notValidForm{
    color: red;
}
.boxTimer{
    height: 10px;
    display: flex;
    justify-content: center;
    align-items: center;
}
.doneAlert{
    background-color: inherit !important;
    margin-top: 40px !important;
}
.lastMessage{
    margin-top: 30px;
    font-size: 15px;
}
