.footer{
    color: black;
    display: flex;
    justify-content: center;
    align-items: center;
    margin: 0 auto;
    flex-direction: column;
}
.wrap__footer{
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
}
.footer__item{
    display: flex;
    justify-content:center;
    align-items: center;
    width: 250px;
    margin-bottom: 10px;
    flex-direction: column;
}
.footer__item p {
    margin: 0 auto;
    font-size: 10px;
}
.footer__item img{
    width: 20px;
    height: 20px;
}
.linksFooter{
    text-decoration: none;

    color: black;
    transition: 0.5s ease;
    border-bottom: none;
}
.linksFooter:hover{
    color: gold;
}