@media only screen and (min-width: 320px) {
.wrapInfo{

}
    .more__item div:nth-child(3n){
        order: 1 ;
    }
}

@media only screen and (max-width: 434px) {

    .item-text {
        background-color: white;
        padding: 7px;
    }
    .main__item{
        margin-bottom: 20px;
    }
    .main{
        background-image: none;
        height: 100%;
    }

    .main__more{
        height: 100%;
        text-align: center;
    }
    .more__item{
        width: 100%;
        flex-direction: column;
    }
    .more__item div{
        width: 100%;
    }


}

@media only screen and (max-width: 1100px) {

    .wrapInfo{
        margin-top: 80px;
    }
    .main{
        height: 100%;
        padding: 0;
    }
    .item-text {
        background-color: white;
    }

    .main__more{
        flex-direction: column;
        height: auto;
    }
    .main__more p{
        line-height: 20px;
        width: 100%;
    }

    .aboutAnn{
        margin-bottom: 0;
    }
    .detail{
        margin: 0;
    }
    .instagram_wrap{
        margin-bottom: 0;
    }
    .more__item{
        width: 100%;
        height: 100%;
        text-align: center;
        margin-bottom: 20px;
    }
    .bcg__more{
        width: 50%;
        height: 50%;
    }

    .more__item img{
        width: 100%;
        height: 100%;
    }
    .more__item{
        width: 100%;
        flex-direction: column;
    }
    .more__item div{
        width: 100%;
    }

}
@media only screen and (min-width: 1350px) {
    .more__item div:nth-child(3n) {
        order: 0;
    }
    .main{
        height: 100%;
    }
    .more__item div:nth-child(3n){
        order: 0;
    }
    .main__item:last-child{
        margin-bottom: 100px;
    }
}